import React from 'react';
import bubble from './bubble.PNG'
import reset from './reset.png'
import start from './start.png'
import reload from './reload.png'
const About=()=>{return(
    <>
        <div className='container'>
        <h3 Style="text-align:center;">ALGORITHM VISUALIZATION PLATFORM</h3>
        <p>
            In addition to the mathematical and empirical 
           analyses of algorithms, there is yet a third 
           way to study algorithms. It is called algorithm 
           visualization and can be defined as the use of 
           images to convey some useful information about 
           algorithms. That information can be a visual illustration 
           of an algorithm’s operation, of its performance on different 
           kinds of inputs, or of its execution speed versus that of 
           other algorithms for the same problem. To accomplish this 
           goal, an algorithm visualization uses graphic elements points, 
           two-dimensional data-circles, and so on to represent some 
           “interesting events” in the algorithm’s operation.
        </p>
        <h3 Style="text-align:center;">How to use</h3>
        <p>The following is the window you will see when you use algorithm 
            visualization platform to visualize algorithms. This window consists of 
            six data circles initially all set to zero, and it consists of three buttons i.e. reset, start, reload.
        </p>
        &nbsp;<img src={bubble} Style='height:275px; width:500px;margin-left:auto;margin-right:auto;display:block;'/>
        <br/><br/><br/>
        <p>Following image shows the reset button, clicking on this reset button will 
            set the data-circles to different random numbers. Each time you click on 
            reset button six data-circles will be set to new random values. 
        </p>
        &nbsp;<img src={reset} Style='height:275px; width:500px;margin-left:auto;margin-right:auto;display:block;'/>
        <br/><br/><br/>
        <p>Following image shows the start button, clicking on this start button will 
            start to visualizing different algorithms using an interactive animation. 
        </p>
        &nbsp;<img src={start} Style='height:275px; width:500px;margin-left:auto;margin-right:auto;display:block;'/>
        <br/><br/><br/>
        <p>Following image shows the reload button, clicking on this reload button will 
            reload the entire page, user can press reload button in case the page is not 
            loaded properly or in case of any unexpected functioning. 
        </p>
        &nbsp;<img src={reload} Style='height:275px; width:500px;image-border:5px;margin-left:auto;margin-right:auto;display:block;'/>
        <br/><br/><br/>
        </div>
    </>
);}
export default About;