import React from 'react';
import {NavLink} from 'react-router-dom'
function Navlist(props){
    if(props.name==='Home'){
        return(
            <li className="nav-item">
                <NavLink to={props.useref} className="nav-link dropdown active" >{props.name}</NavLink>
            </li>
        );
    }
    else if(props.name==='Bubble'){
        return(
            <li className="nav-item">
                <NavLink to={props.useref} className="nav-link dropdown active" >{props.name}</NavLink>
            </li>
        );
    }
    else if(props.name==='Insertion'){
        return(
            <li className="nav-item">
                <NavLink to={props.useref} className="nav-link dropdown active">{props.name}</NavLink>
            </li>
        );
    }
    else if(props.name==='Selection'){
        return(
            <li className="nav-item">
                <NavLink to={props.useref} className="nav-link dropdown active">{props.name}</NavLink>
            </li>
        );
    }
    else if(props.name==='Merge'){
        return(
            <li className="nav-item">
                <NavLink to={props.useref} className="nav-link dropdown active">{props.name}</NavLink>
            </li>
        );
    }
    else if(props.name==='About'){
        return(
            <li className="nav-item">
                <NavLink to={props.useref} className="nav-link dropdown active">{props.name}</NavLink>
            </li>
        );
    }
    else{
        return(
            <li className="nav-item">
                <NavLink to={props.useref} className="nav-link dropdown active">{props.name}</NavLink>
            </li>
        );
    }
}
export default Navlist;